.dashboard-flex-container {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: start;
    margin-left: 20px;
    margin-top: 30px;
    width: 500px;
    position:absolute;
    height: 100%;
}

.dashboard-horizontal-flex-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin: 1.75em;
}

.weather-details{
    display: flex;
    flex-direction: column;
    background-color: white;
    padding: 1em;
    box-shadow: 3px 3px 2px gray;
    width: 100%;
    
}

.text{
    text-align: center;
    margin-bottom: 0;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.city-name{
    font-size: xx-large;
    text-decoration: underline;
}

.temperature, .temp{
    font-size: x-large;
    margin-top: 0.5em;
}

.weather{
    font-size: large;
}


.tile {
    background: white !important;
    box-shadow: 3px 3px 2px grey;
    border-radius: 12px !important;
    margin: 1em;
    width: 200px;
    height: 250px;
}

.canvas{

    width:750px !important;
    height:300px !important;
    margin-left: 600px !important ;
    margin-right: 300px;

  }
  
 